/*-------------------------------------------------------------------
[1. Header]
*------------------------------------------------------------------*/
.ttr-header{
	position: fixed;
	z-index: 99;
	width: 100%;
	left: 0;
	top: 0;
	font-size: 14px;
	height: 60px;
	font-weight: 400;

	/* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#ff8814+0,db4b24+100 */
	background: #ff8814; /* Old browsers */ /* FF3.6-15 */ /* Chrome10-25,Safari5.1-6 */
	background: linear-gradient(to right, #ff8814 0%,#db4b24 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ff8814', endColorstr='#db4b24',GradientType=1 ); /* IE6-9 */
	box-shadow: 2px 2px 5px 0px rgba(0,0,0,0.2); 
	transition: top 300ms cubic-bezier(0.215, 0.610, 0.355, 1.000); /* easeOutCubic */
}
.ttr-header a{
	color: #f37319;
}
.ttr-header.hide{
	top: -60px;
}
.ttr-toggle-sidebar{
	width: 60px;
	height: 60px;
	text-align: center;
	cursor: pointer;
	color: #FFF;
	background-color: rgba(255,255,255,0.1);
	float: left;
}
.ttr-toggle-sidebar i{
	position: absolute;
	left: 18px;
	top: 18px;
	font-size: 20px; 
	transition: -webkit-transform 200ms cubic-bezier(0.215, 0.610, 0.355, 1.000); 
	transition: transform 200ms cubic-bezier(0.215, 0.610, 0.355, 1.000); 
	transition: transform 200ms cubic-bezier(0.215, 0.610, 0.355, 1.000), -webkit-transform 200ms cubic-bezier(0.215, 0.610, 0.355, 1.000); /* easeOutCubic */
}
.ttr-toggle-sidebar .ttr-open-icon{
	-webkit-transform: scale(1);
	        transform: scale(1);
}
.ttr-toggle-sidebar .ttr-close-icon{
	-webkit-transform: scale(0);
	        transform: scale(0);
}
.ttr-opened-sidebar .ttr-toggle-sidebar .ttr-open-icon{
	-webkit-transform: scale(0);
	        transform: scale(0);
}
.ttr-opened-sidebar .ttr-toggle-sidebar .ttr-close-icon{
	-webkit-transform: scale(1);
	        transform: scale(1);
}
.ttr-logo-box{
	display: block;
	float: left;
}
.ttr-logo-box h1{
	margin: 0;
	padding: 0;
	display: inline-block;
}
.ttr-logo{
	font-size: 18px;
	color: #FFF;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	height:60px;
	padding:0 15px;
}
.ttr-logo img{
	display: block;
	float: left;
}
.ttr-logo .ttr-logo-mobile{
	display: none;
}
.ttr-logo .ttr-logo-desktop{
	display: block;
}
.ttr-logo .ttr-logo-name{
	display: inline-block;
	float: left;
	font-weight: 400;
	line-height: 30px;
	margin-left: 10px;
	padding-left: 10px;
	border-left: solid 1px #FFF;
}
.ttr-header-menu{
	display: inline-block;
	float: left;
}
.ttr-header-navigation, .ttr-header-navigation ul{
	list-style: none;
	padding: 0;
	margin:0;
}
.ttr-header-navigation > li{
	float: left;
	position: relative;
}
.ttr-header-navigation > li > a{
	display: table-cell !important;
	vertical-align: middle;
	height: 60px;
	line-height: normal;
	color: #FFF;
	font-weight: 600;
	padding: 0 15px;
	text-decoration: none;
}
.ttr-header-navigation > li > a .material-icons{
    vertical-align: middle;
    top: -1px;
    position: relative;
}
.ttr-header-navigation > li > a.active{
	box-shadow: inset 0 -2px 0 0 #fff;
}
.ttr-header-submenu{
	background-color: #fff;
	position: absolute;
	top: 100%;
	min-width: 100%;
	left: 0;
	box-shadow: 2px 2px 20px 0px rgba(0,0,0,0.3);
	transforttr-origin: left top 0px;
	display: none;
	transition: -webkit-transform .3s ease;
    transition: transform .3s ease;
    transition: transform .3s ease, -webkit-transform .3s ease;
    transition: transform .3s ease,-webkit-transform .3s ease;
    -webkit-transform: scale(0);
    -khtml-transform: scale(0);
    transform: scale(0);
    -ms-box-shadow: 0 2px 4px 0 rgba(0,0,0,.16),0 2px 8px 0 rgba(0,0,0,.12);
    -o-box-shadow: 0 2px 4px 0 rgba(0,0,0,.16),0 2px 8px 0 rgba(0,0,0,.12);
    box-shadow: 0 2px 4px 0 rgba(0,0,0,.16), 0 2px 8px 0 rgba(0,0,0,.12);
    -ms-filter: "progid:DXImageTransform.Microsoft.Matrix(M11=0, M12=0, M21=0, M22=0, SizingMethod='auto expand')";
    filter: progid:DXImageTransform.Microsoft.Matrix( M11=0, M12=0, M21=0, M22=0, SizingMethod='auto expand');
}
.ttr-header-submenu.ttr-extra-menu{
	width: 280px;
}
.ttr-extra-menu a{
	display: block;
	width: 50%;
	float: left;
	height: 80px;
	text-align: center;
	border-bottom:solid 1px #EEE;
	padding: 15px 0;
	color: #333 !important;
}
.ttr-extra-menu a i{
	font-size:24px;
	margin-bottom: 10px;
}
.ttr-extra-menu a:hover{
	background-color: #f2f2f2;
	text-decoration: none;
}
.ttr-extra-menu a span{
	display: block;
}
.ttr-extra-menu a:nth-child(odd){
	border-right: solid 1px #EEE;
}
.ttr-header-submenu.ttr-notify-list{
	width: 360px;
}
.ttr-notify-header{
	width: 100%;
	padding: 15px 10px;
	color: #333;
	background-color: #f4f4f4;
}
.ttr-notify-header .ttr-notify-text-top{
	display: block;
	font-size: 20px;
	text-align: center;
}
.ttr-notify-header .ttr-notify-text{
	display: block;
	text-align:center;
	font-size: 15px;
}
.ttr-notify-tab{
	padding: 15px;
}
.ttr-notify-tab .nav{
	margin-bottom: 15px;
	border-bottom: solid 1px #eee;
}
.ttr-notify-tab .nav li{
	display: inline-block;
	float: left;
	width: auto;
	border-left: none !important;
	margin-right: 20px;
}
.ttr-notify-tab .nav li a:hover{
	background-color: transparent;
	border-bottom: solid 1px #9D50BB;
}
.ttr-notify-tab .nav li a{
	padding: 6px 0;
	font-weight: 500;
	font-size: 16px;
}
.ttr-notify-tab .nav li a.active{
	border-bottom: solid 1px #9D50BB;
	color: #9D50BB;
}
.ttr-header-submenu ul li{
	display: block;
	width: 100%;
}
.ttr-header-submenu ul li a{
	display: block;
	text-decoration: none;
	padding: 10px 15px;
	color: #444;
	white-space: nowrap;
}
.ttr-header-submenu ul li a:hover{
	background-color: #EEE;
}
.ttr-header-submenu.active{
	-webkit-transform: scale(1);
    -khtml-transform: scale(1);
    transform: scale(1);
    -ms-filter: "progid:DXImageTransform.Microsoft.Matrix(M11=1, M12=0, M21=0, M22=1, SizingMethod='auto expand')";
    filter: progid:DXImageTransform.Microsoft.Matrix( M11=1, M12=0, M21=0, M22=1, SizingMethod='auto expand');
}
.ttr-header-right{
	display: inline-block;
	float: right;
}
.ttr-header-right.ttr-with-seperator .ttr-header-navigation li{
	border-left:solid 1px rgba(255,255,255,0.1);
}
.ttr-header-right .ttr-header-navigation .ttr-header-submenu{
	-webkit-transform-origin: right top 0px;
	        transform-origin: right top 0px;
	left: auto;
	right: 0;
}
.ttr-user-avatar{
	display: block;
	width: 32px;
	height: 32px;
	border-radius:100%;
	overflow: hidden;
}
.ttr-search-bar{
	position: absolute;
	width: 100%;
	height: 60px;
	top: -70px;
	left: 0;
	/* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#ff8814+0,db4b24+100 */
	background: #ff8814; /* Old browsers */ /* FF3.6-15 */ /* Chrome10-25,Safari5.1-6 */
	background: linear-gradient(to right, #ff8814 0%,#db4b24 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ff8814', endColorstr='#db4b24',GradientType=1 ); /* IE6-9 */ 
	transition: top 200ms cubic-bezier(0.215, 0.610, 0.355, 1.000); /* easeOutCubic */
}
.ttr-search-bar.active{
	top: 0;
}
.ttr-search-form{
	max-width: 1160px;
	width: 100%;
	height: 40px;
	margin: 10px auto;
	display: block;
	font-size: 14px;
	position: relative;
}
.ttr-search-input-wrapper{
	left: 20px;
	top: 0;
	right: 60px;
	bottom: 0;
	height: 40px;
	position: absolute;
}
.ttr-search-input{
	width: 100%;
	height: 40px;
	background-color: rgba(255,255,255,.2);
	border:none;
	padding: 0 54px 0 14px;
	display: block;
	color: #FFF;
	outline: none;
	font-size: 14px;
	font-family: inherit;
}
.ttr-search-input::-webkit-input-placeholder {
    color: #FFF;
    opacity: .5;
}
.ttr-search-input:-ms-input-placeholder {
    color: #FFF;
    opacity: .5;
}
.ttr-search-input::placeholder {
    color: #FFF;
    opacity: .5;
}
.ttr-search-input:-ms-input-placeholder {
    color: #FFF;
}
.ttr-search-input::-ms-input-placeholder {
    color: #FFF;
}
.ttr-search-input:focus{
	background-color: rgba(255,255,255,.3);
}
.ttr-search-submit{
	position: absolute;
	right: 0;
	top: 0;
	bottom: 0;
	width: 40px;
	height: 40px;
	padding: 8px;
	background: none;
	color: #FFF;
	border:none;
	cursor: pointer;
	outline: none;
}
.ttr-search-submit:hover{
	background-color: rgba(255,255,255,.1);
}
.ttr-search-close{
	width: 40px;
	height: 40px;
	position: absolute;
	right: 20px;
	top: 0;
	background: none;
	border:none;
	padding: 8px;
	outline: none;
	cursor: pointer;
	color: #FFF;
}
