/*-------------------------------------------------------------------
[3. Sidebar]
*------------------------------------------------------------------*/
.ttr-sidebar{
	position: fixed;
	width: 300px;
	top: 0;
	bottom: 0;
	left: 0;
	background-color: #fafafa;
	height: 100%;
	z-index: 101;
	left: -310px;
	color: #ff8814;
	box-shadow: 2px 2px 5px 0px rgba(0,0,0,0.2); 
	transition: all 200ms cubic-bezier(0.215, 0.610, 0.355, 1.000); /* easeOutCubic */
	color: var(--primary);
}
.ttr-opened-sidebar .ttr-sidebar{
	left: 0;
}
.ttr-sidebar-wrapper{
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	overflow-y: auto;
	padding-bottom: 60px;
}
.ttr-sidebar-wrapper::-webkit-scrollbar {
    width: 6px;
}
.ttr-sidebar-wrapper::-webkit-scrollbar-thumb {
    background: #f37319;
}
.ttr-sidebar-wrapper::-webkit-scrollbar-track {
    background: #455a64;
}
.ttr-sidebar-logo{
	width: 100%;
	height: 60px;
	border-bottom: solid 1px #cfd8dc;
}
.ttr-sidebar-logo a{
	display: inline-block;
	float: left;
	margin: 16px 24px;
}
.ttr-sidebar-logo a img{
	display: block;
}
.ttr-sidebar-toggle-button{
	width: 24px;
	height: 24px;
	float: right;
	margin: 18px;
	cursor: pointer;
}
.ttr-sidebar-pin-button{
	width: 24px;
	height: 24px;
	float: right;
	margin: 18px;
	cursor: pointer;
}
.ttr-sidebar-pin-button i{
	position: absolute;
	-webkit-transform: scale(1);
	        transform: scale(1); 
	transition: -webkit-transform 200ms cubic-bezier(0.215, 0.610, 0.355, 1.000); 
	transition: transform 200ms cubic-bezier(0.215, 0.610, 0.355, 1.000); 
	transition: transform 200ms cubic-bezier(0.215, 0.610, 0.355, 1.000), -webkit-transform 200ms cubic-bezier(0.215, 0.610, 0.355, 1.000); /* easeOutCubic */
}
.ttr-sidebar-pin-button .ttr-fixed-icon{
	-webkit-transform: scale(0);
	        transform: scale(0);
}
.ttr-pinned-sidebar .ttr-sidebar-pin-button .ttr-fixed-icon{
	-webkit-transform: scale(1);
	        transform: scale(1);
}
.ttr-pinned-sidebar .ttr-sidebar-pin-button .ttr-not-fixed-icon{
	-webkit-transform: scale(0);
	        transform: scale(0);
}
.ttr-sidebar-navi ul{
	padding: 0;
	margin: 0;
	list-style: none;
}
.ttr-sidebar-navi > ul ul{
	display: none;
}
.ttr-sidebar-navi .inner{
	overflow: hidden;
  	display: none;
}
.ttr-sidebar-navi .ttr-seperate{
	width: 100%;
	height: 1px;
	background-color: #cfd8dc;
}
.ttr-sidebar-navi li{
	margin: 2px 0;
	display: block;
	width: 100%;
}
.ttr-sidebar-navi a{
	line-height: 50px;
	text-decoration: none;
	height: 50px;
	padding: 0 20px;
	color: #333;
	white-space: nowrap;
	display: table;
	width: 100%;
}
.ttr-sidebar-navi a:hover{
	background-color: #EEE;
}
.ttr-sidebar-navi a > span{
	display: table-cell; 
}

.ttr-sidebar-navi a > span.ttr-icon{
    width: 40px;
    height: 50px;
    line-height: 0;
    vertical-align: middle;
	font-size: 18px;
}
.ttr-sidebar-navi a > span.ttr-arrow-icon{
	width: 24px;
    height: 50px;
    line-height: 0;
    vertical-align: middle;
    transition: -webkit-transform 200ms cubic-bezier(0.215, 0.610, 0.355, 1.000);
    transition: transform 200ms cubic-bezier(0.215, 0.610, 0.355, 1.000);
    transition: transform 200ms cubic-bezier(0.215, 0.610, 0.355, 1.000), -webkit-transform 200ms cubic-bezier(0.215, 0.610, 0.355, 1.000);
	text-align: center;
	font-size: 18px;
}
.ttr-sidebar-navi > ul > li > ul > li > a{
	padding: 0 20px 0 60px;
	line-height: 40px;
	height: 40px;
	font-size: 95%;
}
.ttr-sidebar-navi > ul > li > ul > li > a span.ttr-arrow-icon{
	height: 40px;
}
.ttr-sidebar-navi > ul > li > ul > li > ul > li > a{
	padding: 0 20px 0 80px;
	line-height: 40px;
	height: 40px;
}
.ttr-sidebar-navi ul li.show > a > .ttr-arrow-icon{
	-ms-transform: rotate(180deg); /* IE 9 */
    -ms-transform-origin: 50% 50%; /* IE 9 */
    -webkit-transform: rotate(180deg); /* Safari 3-8 */
    -webkit-transform-origin: 50% 50%; /* Safari 3-8 */
    transform: rotate(180deg);
    transform-origin: 50% 50%;
}
.ttr-sidebar-navi ul li.show > a{
	background-color: #EEEEEE;
}
.ttr-sidebar-navi>ul>li>ul>li.show > a{
	background-color: #f4f4f4;
}
.ttr-sidebar-navi ul li.show > a{
	color: var(--primary);
}
.ttr-overlay{
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	background: rgba(0,0,0,.5);
	z-index: 100;
	cursor: pointer;
	display: none;
}
/*pinned sidebar styles */
.ttr-opened-sidebar .ttr-overlay{
	display: block;
}
.ttr-pinned-sidebar .ttr-overlay{
	display: none !important;
}
.ttr-pinned-sidebar .ttr-sidebar{
	top: 60px;
}

.ttr-pinned-sidebar.ttr-opened-sidebar .ttr-wrapper{
	margin-left: 300px;
}
.ttr-pinned-sidebar .ttr-wrapper{
	margin-left: 0;
}


/*-------------------------------------------------------------------
[4. Material effect class helpers]
*------------------------------------------------------------------*/
.ttr-material-button{
	position: relative;
	overflow: hidden;
}

.ttr-wave-effect {
	display: block;
	position: absolute;
	-webkit-transform: translateX(-50%) translateY(-50%);
	      transform: translateX(-50%) translateY(-50%);
	width: 0px;
	height: 0px;
	background-color: #fff;
	border-radius: 50%;
	opacity: 0.7;
}



/*-------------------------------------------------------------------
[5. Mobile fixer styles]
*------------------------------------------------------------------*/
@media (max-width: 768px){
	.ttr-header-submenu.ttr-notify-list{
		width: 300px;
	}
	.ttr-logo .ttr-logo-mobile{
		display: block;
		position: relative;
	}
	.ttr-logo .ttr-logo-desktop{
		display: none;
	}
	.ttr-header-menu{
		display: none;
	}
	.ttr-hide-on-mobile{
		display: none;
	}
	.ttr-header-right .ttr-header-navigation .ttr-header-submenu.ttr-notify-list{
		right: -60px;
	}
}


